nav {
    background: var(--green);
    display: flex;
    justify-content: space-between;
    position: fixed;
    padding: 0 1.5em;
    width: 100%;
    top: 0;
    * {
        color: #fff;
        fill: #fff;
    }
    .navlink-div {
        display: flex;
        margin-left: auto;
        a, div {
            display: flex;
            align-items: center;
            padding: 1em;
            &:hover {
                color: var(--yellow);
                * {
                    fill: var(--yellow);
                }
            }
            align-self: flex-end;
        }
    }
    .login-div, .logo-div {
        display: flex;
        align-items: center;
        padding-left: 1.5em;
        padding-right: 1.5em;
        &:hover {
            background: var(--dark-green)
        }
    }
  }


